import React, { useEffect, useState } from 'react';
import '../index.css';
import star from '../assets/images/star.png';
import astronaut from '../assets/images/hoverboard.png';
import 'animate.css';

import { motion } from 'framer-motion';
import styled, { keyframes } from 'styled-components';
import Astroid from './canvas/Astroid';

const flashAnimation = keyframes`
  0%, 100% {
    opacity: 0;
  }
  50% {
    opacity: 1;
  }
`;

const FlashContainer = styled.div`
  width: 100%;
  height: 100vh;
  background-color: #b1b1b1;
  position: fixed;
  top: 0;
  left: 0;
  opacity: 0;
  box-shadow: inset black;
  animation: ${flashAnimation} 0.5s ease infinite;
`;

const Hero: React.FC = () => {
    const [appear, setAppear] = useState<boolean>(false);
    const [flash, setFlash] = useState(false);

    useEffect(() => {
        setTimeout(() => {
            setAppear(true);
        }, 1816)

        // setTimeout(() => {
        //     setFlash(true);
        // }, 1850);
    }, []);

    useEffect(() => {
        if (flash) {
            const timer = setTimeout(() => {
                setFlash(false);
            }, 270); // Flash duration

            return () => clearTimeout(timer);
        }
    }, [flash]);

    return (
        <section className="hero-container">
             {flash && <FlashContainer />}
            <div className="hero-content">
                <h1 className="animate__animated animate__bounce animate__delay-2s">Frontend Developer</h1>
                <p className='animate__animated animate__fadeInBottomRight animate__delay-2s'>
                    Hey, there! I'm Mike Kwak, a passionate developer for 3D
                    visuals, user interfaces and web applications. I also have a
                    good understanding of HTTP, Backend and Database!
                </p>

                {/* <Astroid/> */}

                <div className='animation'>
                    <div className="astronaut-container">
                        <div className='astronaut-movement'>
                            {appear ? (
                                <img
                                    className="astronaut"
                                    src={astronaut}
                                    alt="astronaut"
                                />
                            ) : (
                                <img className="star" src={star} alt="" />
                            )}
                        </div>
                    </div>
                </div>
            </div>

            <div className="scroll-icon-container">
                <a href="#about">
                    <div className="scroll-icon">
                        <motion.div
                            animate={{
                                y: [0, 24, 0],
                            }}
                            transition={{
                                duration: 1.5,
                                repeat: Infinity,
                                repeatType: 'loop',
                            }}
                            className="circle-icon"
                        />
                    </div>
                </a>
            </div>
        </section>
    );
};

export default Hero;
