import styled from 'styled-components';
import headshot from '../assets/images/headshot.jpg'
import codebackground from '../assets/images/codebackground.jpg'

const Bulb = styled.div`
    height: 15px;
    width: 15px;
    border-radius: 50%;
    box-shadow: inset 2px 2px 5px rgba(255, 255, 255, 0.5);
    background-color: ${props => {
        switch (props.color) {
            case 'green':
                return 'green';
            case 'blue':
                return 'blue';
            case 'yellow':
                return 'yellow';
            default:
                return 'gray';
        }
    }};
`

const About = () => {
    return (
        <>
            <div className="profile-container">
                <div className="profile">
                    <div className="languages">
                        <div className="full-stack">
                            <div className="type">Full Stack</div>

                            <ul>
                                <li className="language">
                                    <i className="devicon-nodejs-plain"></i>
                                    <Bulb color='green' />
                                </li>
                                <li className="language">
                                    <i className="devicon-react-original"></i>
                                    <Bulb color='blue' />
                                </li>
                                <li className="language">
                                    <i className="devicon-html5-plain"></i>
                                    <Bulb color='green' />
                                </li>
                                <li className="language">
                                    <i className="devicon-sass-original"></i>
                                    <Bulb color='green' />
                                </li>
                                <li className="language">
                                    <i className="devicon-threejs-original"></i>
                                    <Bulb color='green' />
                                </li>
                                <li className="language">
                                    <i className="devicon-nextjs-original"></i>
                                    <Bulb color='green' />
                                </li>
                                <li className="language">
                                    <i className="devicon-javascript-plain"></i>
                                    <Bulb color='green' />
                                </li>
                                <li className="language">
                                    <i className="devicon-typescript-plain"></i>
                                    <Bulb color='green' />
                                </li>
                            </ul>


                        </div>
                        <div className="machine-learning">
                            <div className="type">AI / ML</div>
                            <ul>
                                <li className="language">
                                    <i className="devicon-python-plain-wordmark"></i>
                                    <Bulb color='green' />
                                </li>

                                <li className="language">
                                    <i className="devicon-tensorflow-original"></i>
                                    <Bulb color='yellow' />
                                </li>
                            </ul>
                        </div>
                    </div>
                    <div className="profile-pic">
                        <img src={headshot} alt="" />
                    </div>
                </div>
            </div>
            <div className="description">
                <p><i>Lorem ipsum dolor sit ameters consectetur adipisicing elit. </i></p>
            </div>
            <div className="wrapper">
                <div className="center-line">
                    <a href="#" className="scroll-icon"><i className="fas fa-caret-up"></i></a>
                </div>
                <div className="row row-1">
                    <section>
                        <i className="icon fas fa-home"></i>
                        <div className="details">
                            <span className="title">Born in Seoul</span>
                            <span>18th Feb 2004</span>
                        </div>
                        <p>Seoul, South Korea is my hometown. As a child, I had many creative interests.</p>
                        {/* <div className="bottom">
                            <a href="#">Read more</a>
                            <i>- Someone famous</i>
                        </div> */}
                    </section>
                </div>
                <div className="row row-2">
                    <section>
                        <i className="icon fas fa-star"></i>
                        <div className="details">
                            <span className="title">Move to Canada</span>
                            <span>1st Aug 2015</span>
                        </div>
                        <p>During my 5th grade, I made a life-shifting decision of moving to North Vancouver, Canada</p>
                    </section>
                </div>
                <div className="row row-1">
                    <section>
                        <i className="icon fas fa-star"></i>
                        <div className="details">
                            <span className="title">St. Thomas Aquinas</span>
                            <span>Summer 2019</span>
                        </div>
                        <p>At the new high school I transferred to, I met some people and started developing out of interest.</p>
                    </section>
                </div>
                <div className="row row-2">
                    <section>
                        <i className="icon fas fa-star"></i>
                        <div className="details">
                            <span className="title">University of Waterloo</span>
                            <span>Fall 2022</span>
                        </div>
                        <p>I'm currently in the Software Engineering Co-op program at Waterloo, Ontario.</p>
                    </section>
                </div>

                {/* <img src={codebackground} style={{opacity: 0.2}}alt="" /> */}
                

            </div>
        </>
    )
}

export default About;